import 'bootstrap/dist/css/bootstrap.min.css';
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'
import Button from 'react-bootstrap/Button'
import Form from 'react-bootstrap/Form'
import Container from 'react-bootstrap/Container'
import logo from '../logo.png'

const GreetingForm = props => {
    return(
        <Container style={{color: 'white'}}>
            <img alt="logo" src={logo} style={{width:'30%'}}/>
            <br/><br/><br/>
            <h2>مدارس التهذيب الأهلية تهنئكم بحلول شهر رمضان المبارك</h2>
            <h2>تقبل الله صيامكم وقيامكم وأعاده عليكم في أحسن الأحوال</h2>
            <br/><br/><br/>
            <Form className="justify-content-md-center">
                <Form.Label column="lg">احصل عل بطاقة التهنئة بالشهر الفضيل باسمك</Form.Label>
                <InputGroup className="mb-3">
                    <FormControl
                        placeholder="ادخل اسمك هنا"
                        aria-label="ادخل اسمك هنا"
                        aria-describedby="greeter-name"
                        onChange={props.onChange}
                    />
                    <InputGroup.Append>
                        <Button variant="primary" onClick={props.onSubmit}>إرسال</Button>
                    </InputGroup.Append>
                </InputGroup>
            </Form>
        </Container>
    )
}

export default GreetingForm