import greetingCard from '../ramadan_card.jpg';
import React, { useRef, useEffect } from 'react';
import Button from 'react-bootstrap/Button'
import ReactGA from 'react-ga';

const saveCanvas = (event) => {
    ReactGA.event({
        category: 'User',
        action: 'Downloaded a greeting card',
        value: event.target.value
    });
    const canvasSave = document.getElementsByTagName('canvas')[0];
    const d = canvasSave.toDataURL('image/png');
    const a = document.createElement("a")
    a.href = d;
    a.download = 'RamadanCard.png';
    window.document.body.appendChild(a);
    a.click();
    window.document.body.removeChild(a);
}

const Canvas = props => {
  
  const canvasRef = useRef(null)
  
  useEffect(() => {
    const canvas = canvasRef.current
    const context = canvas.getContext('2d')
    context.textAlign = "center"
    context.font = "Bold 64px Cairo"
    context.fillStyle = "white"
    context.fillText("جاري تحضير التهنئة", 600, 600)
    //Our first draw
    const image = new Image()
    image.src = greetingCard
    image.onload = () => {
        context.drawImage(image, 0,0)
        context.fillStyle = "#ffd527"
        context.fillText(props.greeter, 600, 1100)
        image.setAttribute('crossorigin','anonymous')
    }
  }, [])
  
  return (
        <div>
            <canvas ref={canvasRef} width="1200px" height="1200px" {...props}/>
            <br/><br/>
            <Button variant="warning" size="lg" value={props.greeter} onClick={saveCanvas}>احفظ كرت التهنئة</Button>
            <br/><br/>
            
        </div>
  )
}

export default Canvas