import Canvas from './components/Canvas'
import background from './home_bg.jpg'
import backgroundOverlay from './bg-overlay.png'
import GreetingForm from './components/GreetingForm'
import './App.css';
import React from 'react';
import ReactGA from 'react-ga';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state = {greeter: '', displayForm: true};
    ReactGA.initialize('G-50P35RC49G');
    ReactGA.pageview(window.location.pathname + window.location.search);
  }

  handleChange(e) {
    this.setState({greeter: e.target.value});
  }

  handleSubmit(e) {
    ReactGA.event({
      category: 'User',
      action: 'Requested a greeting card',
      value: this.state.greeter
    });
    this.setState({displayForm: false});
  }

  render () {
    const displayForm = this.state.displayForm;
    return (
      <div style={{textAlign: 'center', backgroundColor: '#0b091f'}}>
      { displayForm ?
        <div className="App" style={{ backgroundImage: `url(${background})`, backgroundSize: 'cover',   }}>
          <header className="App-header" style={{ position: 'fixed', width: '100%', height: '100%', backgroundImage: `url(${backgroundOverlay})`, overflowY: 'scroll'}}>
            <GreetingForm onChange={this.handleChange} onSubmit={this.handleSubmit}/>
          </header>
        </div>
        : <Canvas greeter={this.state.greeter}/>
      }
      </div>
    );
  }
}

export default App;
